.video {
  font-family: "PT Sans", "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
}

.video__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.video__discription {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}

.video__picture img {
  width: 280px;
  height: 177px;
}

@media (min-width: @tablet-width) {
  .video__picture img {
    width: 648px;
    height: 500px;
  }
}

@media (min-width: @desktop-width) {
  .video__picture img {
    width: 800px;
    height: 500px;
  }
}
